@use 'global' as *;

.news-content {
  padding: rem(14) rem(55) rem(11) rem(16);
  position: relative;
  @include mq('md') {
    display: flex;
    align-items: center;
    gap: rem(40);
    padding: rem(40) rem(83) rem(40) 0;
  }
}

.news-content::after {
  content: "";
  position: absolute;
  right: rem(16);
  top: 50%;
  transform: translateY(-50%);
  width: rem(10.75);
  height: rem(21.5);
  background: transparent url(../images/common/news-arrow.png) no-repeat center center / contain;
}

.news-content__meta {
  display: flex;
  align-items: center;
  gap: rem(24);
  @include mq('md') {
    gap: rem(40);
    flex-shrink: 0;

  }
}

.news-content__date,
.news-content__category {
  color: $black;
  display: inline-block;
  //line-height: 1;
}

.news-content__date {
  font-size: rem(14);
  font-family: $english-family;
  line-height: calc(20 / 14);
  letter-spacing: 0.35px;
  @include mq('md') {
    font-size: rem(18);
    line-height: calc(26 / 18);
    letter-spacing: 0.45px;
    // min-width: rem(98);
    // width: 100%;
  }
}

.news-content__category {
  font-size: rem(12);
  letter-spacing: 0.3px;
  border-radius: 50px;
  line-height: 1;
  border: 2px solid $blue;
  padding: rem(2) rem(14);
  //margin-left: rem(24);
  font-weight: $medium;
  flex-shrink: 0;
  color: $black;
  @include mq('md') {
    font-size: rem(14);
    letter-spacing: 0.35px;
    padding: rem(6) rem(30);
    //margin-left: rem(32);
  }
}

.news-content__link {
  margin-top: rem(8);
  // line-height: calc(20 / 14);
  line-height: 1.25;
  // max-width: rem(700);
  // width: 100%;
  @include mq('md') {
    margin-top: rem(0);
    //margin-left: rem(40);
    line-height: calc(28 / 18);
    max-width: rem(698);
    width: 100%;
  }
}

.news-content__text {
  font-size: rem(14);
  font-weight: $medium;
  color: $black;
  @include mq('md') {
    font-size: rem(18);
    letter-spacing: 0.45px;
  }
}